.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  background-color: #e0f2f1; /* Light teal background */
}

.activity-wrapper {
  width: 100%;
  height: 100%; /* Full viewport height */
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.activity-wrapper:hover {
  transform: scale(1.02);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.activity-wrapper a {
  text-decoration: none;
  color: inherit;
  display: block;
  height: 100%;
}

.activity-section {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.activity-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.activity-wrapper:hover .activity-image {
  transform: scale(1.05);
}

.activity-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.5);
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 2em;
  font-weight: bold;
  color: white;
  text-align: center;
}

.header-title {
  font-size: 2.5em;
  font-weight: bold;
  color: #004d40; /* Teal color for headings */
  margin-bottom: 20px;
}

.header-subtitle {
  font-size: 1.2em;
  color: #004d40; /* Teal color for subtitles */
  margin-bottom: 40px;
  text-align: center;
  max-width: 800px;
}
