.sign-up-container {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  padding: 30px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.progress-bar {
  width: 100%;
  background-color: #f0f0f0;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 20px;
}

.progress {
  height: 8px;
  background-color: #004d40; /* Teal color */
  transition: width 0.3s ease;
}

.step-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.step-container h2 {
  margin-bottom: 20px;
  color: #004d40; /* Teal color */
}

.account-type-selection {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.account-type-selection label {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.account-type-selection input {
  margin-right: 10px;
}

.steps-buttons {
  display: flex;
  gap: 10px;
}

.custom-button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #004d40; /* Teal background */
  color: white;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.custom-button:hover {
  background-color: #002d20; /* Darker teal background */
}

.custom-button[disabled] {
  background-color: #ccc;
  cursor: not-allowed;
}

textarea {
  width: 100%;
  height: 100px;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: none;
}

.error {
  color: red;
  margin-top: -10px;
  margin-bottom: 10px;
}
